@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.streamer-list {
    margin-left: 10vw;
    width: 90vw;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-template-rows: 33% 250px auto;
    justify-content: center;
    background-image: url("https://i.pinimg.com/originals/75/5c/57/755c576d50d64770a04e703402eddfc3.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.edit-schedule-container {
   display: grid;
   grid-template-columns: repeat(1, 100%);
}

.display-schedule-container{
   background-color: #442d6b;
   margin-top: 5%;
}
ul li{
    float: left;
    width: 10%;
    background: #442d6b;
}

 ul{
    list-style-type: disc;
    background: #442d6b; 
}


.form-display-schedule-line-row {
    /* display: flex;
    flex-direction: row;
    margin-bottom: 5%; */
    margin: 2%;
    margin-bottom: 5%;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(2000px, 7fr));
}

.form-display-schedule-card {
    width: 20%;
    height: 25%;
    display: flex;
    margin-left: 2%;
    flex-direction: column;
    border: 1px #442d6b solid;
    /* border-radius: 10px; */
}

.form-display-schedule-header {
    height: 30%;
    text-align: center;
    font-size: 12px;
    display: flex;
    flex-direction: row;
    /* border-radius: 10px; */
}

.form-display-schedule-container {
    padding: 2px 2px;
    display: flex;
    justify-content: center;
    height: 1px;
    margin: 0;
    align-items: center;
    
}

.form-display-schedule-content{
    padding: 10% 10%;
} 

.main-card {
    width: 100vw;
    margin: 0px;
}

.container-card {
   color: "white";
   display: "flex";
   flex-direction: "column";
}

.fliptheCard {
    -webkit-animation: tilt-in-top-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: tilt-in-top-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    width: 200px;
    padding: 5px;
    margin: 0;
}

.form-display-card {
    margin-left:5%;
}

@-webkit-keyframes tilt-in-top-1 {
    0% {
      transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      opacity: 0;
    }
    100% {
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
    }
  }

@keyframes tilt-in-top-1 {
    0% {
      transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      opacity: 0;
    }
    100% {
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
    }
  }
  
.wall-card {
    -webkit-animation: roll-in-top 1s ease-out both;
            animation: roll-in-top 1s ease-out both;
    display: flex;
    width:100vw;
    justify-content: center;
    margin-top: 4%;
}

@-webkit-keyframes roll-in-top {
    0% {
      transform: translateX(-800px) rotate(-540deg);
      opacity: 0;
    }
    100% {
      transform: translateX(0) rotate(0deg);
      opacity: 1;
    }
  }

@keyframes roll-in-top {
    0% {
      transform: translateX(-800px) rotate(-540deg);
      opacity: 0;
    }
    100% {
      transform: translateX(0) rotate(0deg);
      opacity: 1;
    }
  }
*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{-webkit-animation-duration:.01ms !important;animation-duration:.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: "Manrope", sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
}

ul {
    list-style-type: none;
}

.margin-left {
    margin-left: 2%;
}

.horizontal {
    display: flex;
}
